import axios from 'axios';
import { PROD_API_BASE_URL } from '../global_variables'

async function AxiosApi(mappingUrl, reqType, reqData) {
    let resData = [];
    axios.defaults.baseURL = PROD_API_BASE_URL;
    // axios.defaults.withCredentials = true;

    switch (reqType) {
        case 'get':
            await axios.get(mappingUrl, reqData)
                .then(function(response) {
                    resData = response.data;
                })
                .catch(function(error) {
                    console.log(error);
                    resData = false;
                }); break;

        case 'post':
            await axios.post(mappingUrl, reqData)
                .then(function(response) {
                    resData = response.data;
                })
                .catch(function(error) {
                    console.log(error);
                    resData = false;
                }); break;

        case 'put':
            await axios.put(mappingUrl, reqData)
                .then(function(response) {
                    resData = response.data;
                })
                .catch(function(error) {
                    console.log(error);
                    resData = false;
                }); break;

        case 'delete':
            await axios.delete(mappingUrl, reqData)
                .then(function(response) {
                    resData = response.data;
                })
                .catch(function(error) {
                    console.log(error);
                    resData = false;
                }); break;
    }
    
    return ( resData );

} export default AxiosApi;