import React, { useState, useEffect } from 'react';
import { Layout, message } from 'antd';
import '../App.css';

import { CommHeader } from '../header/CommHeader.js';
import { Main } from '../content/Main.js';
import { Login } from '../content/Login.js';
import { Signup } from '../content/Signup.js';

const { Header, Content, Footer } = Layout;

const CommLayout = () => {
  const [cert, setCert] = useState(false);
  const [msg, setMsg] = message.useMessage();
  
  const isLogin = () => {
    setCert(true);
    popMsg();
    loadContent('Main');
  };

  const isLogout = () => {
    setCert(false);
    popMsg();
    loadContent('Main');
  };

  const popMsg = () => {
    msg.open({
      type: 'success',
      content: (cert ? '로그아웃' : '로그인') + ' 되었습니다.',
      duration: 2,
    });
  };

  const [content, setContent] = useState();
  useEffect(() => {
    setContent(<Main />);
  }, []);

  const loadContent = (name) => {
    setContent(
        name === 'Main' ? <Main /> :
        name === 'Login' ? <Login isLogin={isLogin} signupPage={signupPage} /> :
        name === 'Signup' ? <Signup loginPage={loginPage} /> : <></>
      );
  }

  const mainPage = () => { loadContent('Main') };
  const loginPage = () => { loadContent('Login') };
  const signupPage = () => { loadContent('Signup') };

  return (
    <Layout className='layout'>
      <Header className='header'>
        <CommHeader cert={cert} mainPage={mainPage} loginPage={loginPage} isLogout={isLogout} />
      </Header>
      
      <Content className='content'>

        <div
          style={{
            minHeight: 360,
            backgroundColor: '#FFFFFF',
          }}
        >
          {content}
        </div>
      </Content>

      <Footer className='footer'>
        Ant Design ©2023 Created by Ant UED
      </Footer>
      {setMsg}
    </Layout>
  );
}; export { CommLayout };