import React, { useRef } from 'react';
import { Space, Layout, Row, Col } from 'antd';
import '../App.css'
import '../css/login.css';
import AxiosApi from '../util/AxiosApi.js';

const Login = ({isLogin, signupPage}) => {
    const loginRef = useRef([]); // 입력한 id, pw

    // 로그인
    const onLogin = async () => {
        let option = { params: {} };

        loginRef.current.forEach(item => {
            option.params[item.name] = item.value;
        });
        
        const resultData = await AxiosApi(('checkUser'), 'get', option);
        if (resultData === '통신 성공')
            isLogin();
    };

    // 회원가입
    const onSignUp = () => {
        signupPage();
    }

    return (
        <Layout className='layout'>
            <Row className="login-box">
                <Col className="img-area">
                    <h1 className='title'>iPhone 15</h1>
                    <div className="user-box">
                        <p className='sub-title'>2023-09-22(금) 출시 예정</p>
                        <p className='sub-title'>USB-C 포트 탑재</p>
                        <p className='sub-title'>다이나믹 아일랜드 적용</p>
                        <p className='sub-title'>사라진 음소거 버튼과 새로 생긴 액션 버튼</p>
                        <p className='sub-title'>신규 색상 추가</p>
                    </div>
                </Col>
                <Col className='input-area-login'>
                    <h1 className='title'>Search Board</h1>
                    <form>
                        <div className="user-box">
                            <input type="text" name="id" required
                                ref={el => loginRef.current[0] = el}/>
                            <label>ID</label>
                        </div>
                        <div className="user-box">
                            <input type="password" name="pw" required
                                ref={el => loginRef.current[1] = el}/>
                            <label>Password</label>
                        </div>
                        <div className='login-btn'>
                            <Space size={'middle'}>
                                <a onClick={onLogin}>{/*<span/>*/}login</a>
                                <a onClick={onSignUp}>{/*<span/>*/}sign up</a>
                            </Space>
                        </div>
                    </form>
                </Col>
            </Row>
        </Layout>
    );
}; export { Login };