import { useState } from 'react';
import { Modal } from 'antd';

const SuccessModal = ({closeModal}) => {
    const [stts, setStts] = useState(false);

    return (
        <Modal title="Basic Modal" open={true} onOk={closeModal} onCancel={closeModal}>
            <p>Some contents...</p>
            <p>Some contents...</p>
            <p>Some contents...</p>
        </Modal>
    );
};

export { SuccessModal };