import React, { useRef, useState } from 'react';
import { Space, Layout, Row, Col, Input, Modal } from 'antd';
import '../App.css'
import '../css/login.css';
import { UserModel } from '../data/UserData.js';
import AxiosApi from '../util/AxiosApi.js';
import { SuccessModal } from '../modal/SuccessModal.js';

const Signup = ({loginPage}) => {
    const signupRef = useRef([]); // 입력한 id, pw
    const userModel = UserModel;
    const today = '2023-08-18';

    const [Modal, setModal] = useState(false);

    // 회원가입 체크
    const onSignup = async () => {
        let option = { params: userModel };
        
        signupRef.current.forEach(item => {
            option.params[item.input.name] = item.input.value;
        });
        
        const resultData = await AxiosApi(('checkUser'), 'get', option);
        if (resultData === '통신 성공')
            openModal();
    };

    const openModal = () => { setModal(true); };
    const closeModal = () => { setModal(false); loginPage(); };

    return (
        <Layout className='layout'>
            <Row className="login-box">
                <Col className="input-area-signup">
                    <form id='FrmSignup' action='onSignup()'>
                        <div className="user-box">
                            <h1 className='title'>Sign Up</h1>
                        </div>
                        <Row>
                            <Col span={11}>
                                <div className="user-box">
                                    <Input type="text" name="id" required
                                        ref={el => signupRef.current[0] = el}/>
                                    <label>아이디</label>
                                </div>
                            </Col>
                            <Col span={2}></Col>
                            <Col span={11}>
                                <div className="user-box">
                                    <Input type="text" name="name" required
                                        ref={el => signupRef.current[1] = el}/>
                                    <label>이름</label>
                                </div>
                            </Col>
                        </Row>
                        <Row>
                            <Col span={11}>
                                <div className="user-box">
                                    <Input type="password" name="pw" required
                                        ref={el => signupRef.current[2] = el}/>
                                    <label>비밀번호</label>
                                </div>
                            </Col>
                            <Col span={2}></Col>
                            <Col span={11}>
                                <div className="user-box">
                                    <Input type="password" name="chPw" required
                                        ref={el => signupRef.current[3] = el}/>
                                    <label>비밀번호 확인</label>
                                </div>
                            </Col>
                        </Row>
                        <Row>
                            <Col span={11}>
                                <div className="user-box">
                                    <Input type="date" name="birth" required defaultValue={today}
                                        ref={el => signupRef.current[4] = el}/>
                                    <label>생년월일</label>
                                </div>
                            </Col>
                            <Col span={2}></Col>
                            <Col span={11}>
                                <div className="user-box">
                                    <Input type="text" name="phone" required
                                        ref={el => signupRef.current[5] = el}/>
                                    <label>전화번호</label>
                                </div>
                            </Col>
                        </Row>
                        <Row>
                            <Col span={24}>
                            <div className="user-box">
                                    <Input type="text" name="addr" required
                                        ref={el => signupRef.current[6] = el}/>
                                    <label>주소</label>
                                </div>
                            </Col>
                        </Row>
                        <div className='login-btn'>
                            <Space size={'middle'}>
                                <a onClick={onSignup}>
                                    <span/>
                                    Submit
                                </a>
                            </Space>
                        </div>
                    </form>
                </Col>
            </Row>
            <SuccessModal closeModal={closeModal} />
        </Layout>
    );
}; export { Signup };